import React, { useState, useContext, useEffect } from 'react';
import { ICountryStatistic, ICountryJSON, IVizualiztionCountry } from '../../models';
import styles from './styles.module.scss';

export interface ICountryInfoModal {
  coordinates: { x: number; y: number };
  coutryStatistic: IVizualiztionCountry;
  isOpen: boolean;
}

const CountryInfoModal: React.FC<ICountryInfoModal> = (props) => {
  const { coutryStatistic, isOpen, coordinates } = props;
  if (!isOpen) {
    return <></>;
  }
  return (
    <div className={styles.infoModal} style={{ position: 'absolute', top: coordinates.y, left: coordinates.x }}>
      ModalOpen
    </div>
  );
};

export default CountryInfoModal;
